import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Button, Container, Row, Col, Card, Tab, Nav } from "react-bootstrap";
import logo from ".././logo.svg";
import Skills from "./Skills";
import Experience from "./Experience";

function Home() {
  return (
    <>
      <Container>
        <Row>
          <Col style={{ paddingTop: "20px" }}>
            <Card>
              <Card
                style={{
                  height: "150px",
                  background: "black",
                  color: "white",
                }}
              >
                <>
                  <div style={{ fontFamily: "serif", fontSize: "15px" }}>
                    SRIVATSHAN BR.
                  </div>
                  <div style={{ fontFamily: "serif", fontSize: "15px" }}>
                    SOFTWARE ENGINEER
                  </div>
                </>
              </Card>
              <Card.Body>
                <Card.Title>Card Title</Card.Title>
                <Card.Text>
                  Some quick example text to build on the card title and make up
                  the bulk of the card's content.
                </Card.Text>
                <Button variant="primary">Go somewhere</Button>
              </Card.Body>
            </Card>
          </Col>
          <Col style={{ paddingTop: "20px" }} xs={12} md={8}>
            <Tab.Container id="left-tabs-example" defaultActiveKey="first">
              <Card style={{ height: "41px" }}>
                <Nav
                  variant="pills"
                  style={{ fontSize: "15px", paddingLeft: "10px" }}
                >
                  <Nav.Item>
                    <Nav.Link eventKey="first">Experience</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="second">Personal</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="three">Project</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="four">Skills</Nav.Link>
                  </Nav.Item>
                </Nav>
              </Card>
              <br />
              <Card>
                <Card.Body>
                  <Card.Text>
                    <Col sm={9}>
                      <Tab.Content>
                        <Tab.Pane eventKey="first">
                          <Experience />
                        </Tab.Pane>
                        <Tab.Pane eventKey="second">personal details </Tab.Pane>
                        <Tab.Pane eventKey="three">Project details</Tab.Pane>
                        <Tab.Pane eventKey="four">
                          <Skills />
                        </Tab.Pane>
                      </Tab.Content>
                    </Col>
                  </Card.Text>
                  {/* <Button variant="primary">Go somewhere</Button> */}
                </Card.Body>
              </Card>
              <br />

              <Card
                style={{ height: "40px", background: "black", color: "white" }}
              >
                <Container>
                  <Row>
                    <Col
                      style={{
                        textAlign: "left",
                        padding: "10px",
                      }}
                    >
                      <p style={{ fontSize: "12px" }}>Srivatshan BR</p>
                    </Col>

                    <Col
                      style={{
                        textAlign: "right",
                        padding: "10px",
                      }}
                    >
                      <p style={{ fontSize: "12px" }}>
                        <text style={{ color: "wheat" }}> Designed by :</text>{" "}
                        Srivatshan
                      </p>
                    </Col>
                  </Row>
                </Container>
              </Card>
            </Tab.Container>
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default Home;
