import React from "react";
import { table } from "react-bootstrap";

function Experience() {
  return (
    <>
      <div class="panel panel-default">
        <table class="table table-sm  table-bordered table-striped rounded">
          <thead
            style={{
              height: "20px",
              fontSize: "15px",
              color: "white",
            }}
          >
            <tr class="bg-primary">
              <th scope="col">Company Name</th>
              <th scope="col">From</th>
              <th scope="col">To</th>
            </tr>
          </thead>
          <tbody
            style={{ height: "15px", fontSize: "12px" }}
            class="font_style"
          >
            <tr>
              <td>Mindtree Ltd </td>
              <td>Sep 2019 </td>
              <td>Still</td>
            </tr>
            <tr>
              <td>Techunison Software India Pvt Ltd, Coimbatore</td>
              <td>Oct 2017 </td>
              <td>Sep 2019</td>
            </tr>
          </tbody>
        </table>
      </div>
    </>
  );
}

export default Experience;
